<!--  -->
<template>
  <!-- mouse,  -->
  <div
    class="diagramExample"
    @mousedown="startNodesBus($event)"
    @mousemove="moveNodesBus($event)"
    @mouseup="endNodesBus($event)"
  >
    <!--  -->
    <div class="page-left">
      <div class="logo">DAG-Board</div>
      <div
        class="basic-node"
        v-for="(item, i) in initNodesBasic"
        :key="'nodes_basic' + i"
        @mousedown="dragIt(item)"
      >{{item.name}}</div>
    </div>

    <!--  -->
    <div class="headbar">button, , </div>

    <!--  -->
    <div class="right-form">
      <div class="title">, </div>
      <el-form
        v-if="formDetail.currentEditNodeId"
        ref="form"
        :model="formDetail.form"
        label-width="80px"
      >
        <el-form-item label="">
          <el-input v-model="formDetail.form.details"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="formDetail.form.createTime"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit"></el-button>
        </el-form-item>
      </el-form>
    </div>

    <!-- DAG-Diagram -->
    <DAGBoard
      :DataAll="yourJSONDataFillThere"
      @updateDAG="updateDAG"
      @editNodeDetails="editNodeDetails"
      @doSthPersonal="doSthPersonal"
    ></DAGBoard>

    <!--  -->
    <node-bus
      v-if="dragBus"
      :value="busValue.value"
      :pos_x="busValue.pos_x"
      :pos_y="busValue.pos_y"
    />
  </div>
</template>

<script>
import { diagramExampleData, JSONFromService, nodesBasic } from './data.js'

export default {
  components: {},
  props: {},
  data () {
    return {
      data: this.diagramExampleData,
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      initNodesBasic: nodesBasic,
      // svg
      dragBus: false, // ,
      yourJSONDataFillThere: { // 
        nodes: [],
        sides: []
      },
      // 
      busValue: {
        value: "name",
        pos_x: 100,
        pos_y: 100
      },
      // 
      formDetail: {
        currentEditNodeId: null,
        form: {}
      }
    }
  },
  created () {
    this.loadJSON()
  },
  mounted () {
  },
  methods: {
    updateDAG (data, action, id) {
      console.log(...arguments)
      console.log(JSON.stringify(arguments[0]))
      switch (action) {
        case 'selectNode':
          this.showNodeDetails(data.nodes.find(item => item.id === id))
          break;
        default: () => {}
      }
    },
    editNodeDetails() {
      console.log(...arguments)
    },
    doSthPersonal() {
      console.log(...arguments)
    },
    loadJSON() {
      // 
      setTimeout(() => {
              this.yourJSONDataFillThere = JSONFromService
      }, 500);
    },
    /**
     * 
     */
    startNodesBus(e) {
      /**
       *  , 
       * dragDes: {
       *    drag: true,
       *    name: 
       *    type: 
       *    model_id: 
       * }
       **/
      let dragDes = null;
      if (sessionStorage["dragDes"]) {
        dragDes = JSON.parse(sessionStorage["dragDes"]);
      }
      if (dragDes && dragDes.drag) {
        const x = e.pageX;
        const y = e.pageY;
        this.busValue = Object.assign({}, this.busValue, {
          pos_x: x,
          pos_y: y,
          value: dragDes.name
        });
        this.dragBus = true;
      }
    },
    moveNodesBus(e) {
      // 
      if (this.dragBus) {
        const x = e.pageX;
        const y = e.pageY;
        this.busValue = Object.assign({}, this.busValue, {
          pos_x: x,
          pos_y: y
        });
      }
    },
    endNodesBus (e) {
      // svg
      let dragDes = null;
      if (sessionStorage["dragDes"]) {
        dragDes = JSON.parse(sessionStorage["dragDes"]);
      }
      if (dragDes && dragDes.drag && e.toElement.id === "svgContent") {
        const pos_x =
          (e.offsetX - 90 - (sessionStorage["svg_left"] || 0)) /
          (sessionStorage["svgScale"] || 1); // 
        const pos_y =
          (e.offsetY - 15 - (sessionStorage["svg_top"] || 0)) /
          (sessionStorage["svgScale"] || 1); // 
        delete dragDes.drag
        const params = {
          model_id: sessionStorage["newGraph"],
          desp: {
            pos_x,
            pos_y,
            name: this.busValue.value,
            ...dragDes
          }
        };
        this.yourJSONDataFillThere.nodes.push({
          ...params.desp,
          id: this.yourJSONDataFillThere.nodes.length + 100, // , id, id
          in_ports: [0],
          out_ports: [0]
       })
      }
      window.sessionStorage["dragDes"] = null;
      this.dragBus = false;
    },
    dragIt(val) {
      val.form.createTime = new Date().toDateString()
      sessionStorage["dragDes"] = JSON.stringify({
        drag: true,
        ...val
      });
    },
    /**
     * form
     */
    showNodeDetails(val) { // 
      const { id, form } = val
      if (!form) return
      this.formDetail = {
        currentEditNodeId: id,
        form
      }
    },
    onSubmit() { // 
      const { currentEditNodeId, form } = this.formDetail
      this.yourJSONDataFillThere.nodes.map(item => {
        if (item.id === currentEditNodeId) {
          item.form = form
        }
      })
      let _data = this.yourJSONDataFillThere.nodes.find(item => item.id === currentEditNodeId).form
      let str = JSON.stringify(_data, null, 4)
      alert('' + str)
    }
  }
}
</script>

<style lang="scss" scoped>
.diagramExample {
   width: 100%;
   height: 100vh;
   overflow: hidden;
   position: relative;
   .basic-node {
      margin-top: 5px;
      background: #fff;
      color:black;
      border-radius: 15px;
      height: 30px;
      width: 140px;
      border: 1px solid #289de9;
      line-height:30px;
      display: inline-block;
      cursor: pointer;
      user-select: none;
   }
   .page-left {
     position: absolute;
     left: 0;
     top: 0;
     width: 200px;
     height: 100vh;
     background: #717171;
     .logo {
       font-size: 20px;
       line-height: 40px;
       font-weight: bold;
       border-bottom: 1px solid #ccc;
       background: #fff;
     }
   }
   .headbar {
     position: absolute;
     top: 0;
     left: 200px;
     right:0;
     line-height: 40px;
     text-align: left;
     text-indent: 20px;
     user-select:none;
   }
   .right-form {
     width: 400px;
     position: absolute;
     right: 0;
     top: 0;
     height: 100vh;
     border-left: 1px solid #ccc;
     background: #fff;
     .title {
       font-size: 20px;
       line-height: 80px;
     }
   }
}
/** svg */
#svgContent {
  background-size: 50px 50px;
  background-image: linear-gradient(0deg, transparent 24%, rgba(255, 255, 255, 0.05) 25%, rgba(255, 255, 255, 0.05) 26%, transparent 27%, transparent 74%, rgba(255, 255, 255, 0.05) 75%, rgba(255, 255, 255, 0.05) 76%, transparent 77%, transparent), linear-gradient(90deg, transparent 24%, rgba(255, 255, 255, 0.05) 25%, rgba(255, 255, 255, 0.05) 26%, transparent 27%, transparent 74%, rgba(255, 255, 255, 0.05) 75%, rgba(255, 255, 255, 0.05) 76%, transparent 77%, transparent);
  background-color: rgb(60, 60, 60) !important;
  margin-left:200px;
  margin-top: 40px;
  margin-right: 200px;
}
</style>



